export default {
	data(){
		return {
			title: '待支付',
			type: 1, // 支付类型 1门票、2火车票......
			orderInfo: { // 门票信息
				orderNum: '157923864234234',
				productName: '成人票',
				gameTime: '2020/01/20',
				orderPrice: 19.9,
				allPrice: 19.9
			},
			gaotieInfo:[
				{
					startStation: '赵松口',
					endStation: '保定',
					name: '张三',
					nameId: '51392387398632423',
					phone: '17683898932'
				}
			],
			isPayType: false, // 选择支付方式弹窗
			payTpe: 2, // 支付方式 1微信支付、2支付宝支付
			counTimes: '', // 订单支付剩余时间，
			minute: '29', // 倒计时分钟数
			second: '59', //倒计时秒数
			times: null, // 定时器
			isEnd: false, // 倒计时是否结束
			ddd: 12777, // 临时时间
		}
	},
	onLoad(opt){
		// 接受传递参数
		if(opt.type){
			console.log('待支付', opt.type)
			this.type = opt.type
		}
		this.getRemTimes(this.ddd)
		this.countDown()
	},
	onUnload() {
		clearInterval(this.times)
	},
	methods:{
		goback(){
			uni.showModal({
				title: '提示',
				content: '还未完成支付，是否继续退出？',
				success(res) {
					if(res.confirm){
						uni.navigateBack({})
					}
				}
			})
			
		},
		// 获取并计算剩余时间
		getRemTimes(time){
			let allSecond = time / 1000 // 总共秒数
			let allMinute = allSecond / 60 // 总共每分钟数
			let minute = parseInt(allMinute) // 目标分钟数
			let newSecond = '0.' + allMinute.toString().replace(/\d+\.(\d*)/,"$1")
			let second = Math.ceil( (+newSecond) * 60 ) // 目标秒数
			this.second = second
			this.minute = minute
		},
		// 倒计时
		countDown(){
			let _this = this
			this.times = setInterval(() => {
				if(_this.second == 0){
					_this.second = 60
					_this.minute--
				}
				_this.second--
				if(_this.minute == 0 && _this.second == 0){
					_this.isEnd = true // 计时结束
					clearInterval(_this.times)
					uni.showModal({
						title: '提示',
						content: '订单倒计时已结束，当前订单已关闭',
					})
					uni.switchTab({
						url: '../../pages/index/index'
					})
				}
				
			}, 1000)
			
		},
		// 支付方式
		payClick(){
			this.isPayType = true
		},
		// 取消订单
		closeOrder(){
			uni.showModal({
				title: '提示',
				content: '是否取消当前订单？',
				success(res) {
					if(res.confirm){
						// 取消订单操作，待完善
						
					}
				}
			})
		},
		// 选择支付方式
		choosePayType(index){
			console.log(index)
			this.payTpe = index
		},
		// 关闭选择支付方式
		closePayChoose(){
			this.isPayType = false
		},
		// 选择付款方式后立即付款
		paySubClick(){
			console.log('支付方式',this.payTpe)
			if(this.payTpe == 2){
				// 支付宝支付链接
			}else{
